/*
 * check - 
 *
 * @prop initVal -  checkbox 
 * @prop param - 
 * @prop initOpt - 
 * @prop disabled - 
 * @prop required - 
 * @prop theme - 
 * @prop multiple - 
 *
 * @prop errorMessage - checkbox 
 * @prop valName -  checkboxItems  value  key 
 * @prop txtName -  checkboxItems  text  key 
 *
 * @prop beforeCheck - 
 * @prop success - 
 *
 * @prop checkAll -  checkbox 
 *
 */

import './Check.scss'
import './Check.material.scss'
import './Check.bootstrap.scss'

import Vue from 'vue'
import render from './Check.render'
import compEvent from '../../config/event.json'

import Col from '../Col/Col'
import Row from '../Row/Row'
import Icon from '../Icon/Icon'
import MotionRip from '../MotionRip/MotionRip'

import tip from '../Message/tip'

import baseMixin from '../../mixin/base'
import formMixin from '../../mixin/form'
import apiMixin from './Check.api.js'

import {
  isEmpty as isEmptyArray
} from '../../util/data/array'

const TYPE_RADIO = 'radio'
const TYPE_CHECKBOX = 'checkbox'

let checkCompConfig = {
  name: 'Check',

  mixins: [baseMixin, formMixin, apiMixin],

  render,

  components: {
    column: Col,
    row: Row,
    icon: Icon,
    'motion-rip': MotionRip
  },

  props: {
    initOpt: {
      type: Array,
      default: () => []
    },
    inputName: {
      type: String,
      default: ''
    },
    multiple: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    param: {
      type: String,
      default: ''
    },
    errorMessage: {
      type: String,
      default: ''
    },
    required: {
      type: Boolean,
      default: false
    },
    initVal: [Number, Array],
    beforeCheck: Function,
    success: Function,
    valName: {
      type: String,
      default: 'value'
    },
    txtName: {
      type: String,
      default: 'text'
    },
    checkAll: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      compName: 'check', // 
      index: {}, //  index TODO: index 
      value: {}, // check  value 
      text: {}, // check  text 
      option: [], // check 
      oldValue: [], // check  value 
      verified: true, // 
      motion: [], // 
      allowFocus: true, //  focus 
      dangerTip: '',
      slotItems: []
    }
  },

  computed: {
    cPrefix() {
      return `${this.compPrefix}-check`
    },
    isCheckbox() {
      return this.multiple
    },
    isRadio() {
      return !this.multiple
    },
    checkedAll() { // 
      if (this.checkAll && this.multiple) {
        return this.value.length === this.option.length
      }
    },
    checkIconName() {
      switch (this.ui) {
        case 'bootstrap':
          return {
            radio: {
              uncheck: 'circle',
              checked: 'circle-check'
            },
            checkbox: {
              uncheck: 'square-bs',
              checked: 'square-check-bs'
            }
          }
        case 'material':
        default:
          return {
            radio: {
              uncheck: 'circle-o',
              checked: 'circle-check-o'
            },
            checkbox: {
              uncheck: 'square-o',
              checked: 'square-check'
            }
          }
      }
    }
  },

  watch: {
    value(val) {
      this._initCheckbox()
    },
    initVal(val) {
      this.value = val
    },
    initOpt(val) {
      this.option = val
      this._initCheckbox()
    }
  },

  methods: {
    /**
     *  data 
     */
    _setDataOpt() {
      if (typeof this.initVal === 'object') {
        this.value = Object.assign([], this.initVal)
      } else {
        this.value = this.initVal
      }

      this.option = Object.assign([], this.initOpt)
      this.motion = this.initOpt.map(() => {
        return false
      })
    },

    /**
     * 
     *
     * @param {Boolean} check - 
     * @param {Boolean} multiple - 
     */
    _getIconName(check = false, multiple = this.multiple) {
      if (this.multiple) {
        return check ? this.checkIconName.checkbox.checked :
          this.checkIconName.checkbox.uncheck
      } else {
        return check ? this.checkIconName.radio.checked :
          this.checkIconName.radio.uncheck
      }
    },

    /**
     * checkbox
     *
     * @return {Function}
     **/
    _initCheckbox() {
      if (this.isCheckbox) {
        if (!Array.isArray(this.value)) {
          this.index = []
          this.text = []
          this.value = []
          this.oldValue = []
        }

        this.setText()
        this.verified = !this.required || this.value.length !== 0
      } else {
        if (!this.value && this.value !== 0) {
          this.index = undefined
          this.value = undefined
          this.oldValue = undefined
        } else {
          this.setIndex()
          this.setText()
        }

        if (this.required) {
          this.verified = this.value !== 'undefined'
        }
      }
    },

    /**
     * checkboxItems
     *
     * @return {Function, Object}
     **/
    _initCheckboxItems() {
      if (!this._slotContents && !(!!this.$options._content && this.$options._content.innerHTML)) {
        return false
      }

      let $checkboxSlot = {}
      let optionContent = this.$options._content ? this.$options._content.innerHTML : ''
      let $checkboxItemSlot = $(this.$el).find('.checkbox-items-slot')

      if (optionContent) {
        $checkboxSlot = $checkboxItemSlot.html(optionContent)
      } else {
        console.warn('vm.$options._content , ')
        $checkboxSlot = $checkboxItemSlot.html(this._slotContents.default)
      }

      let $checkEles = $checkboxSlot.find('check-ele')

      if ($checkEles.length === 0) {
        return this
      }

      let items = []
      let checkboxItemsEmpty = isEmptyArray(this.option)

      $checkEles.each((index, el) => {
        let $el = $(el)
        let val = $el.attr('value')
        let txt = ''

        val = isNaN(val) ? val : Number(val)

        if ($el[0].hasAttribute('text')) {
          txt = $el.attr('text').trim()

          //  html  text 
          this.slotItems.push($el.html().trim())
        } else {
          txt = $el.text().trim()
        }

        if (checkboxItemsEmpty) {
          items.push({
            value: val,
            text: txt
          })
        }
      })

      $checkboxItemSlot.html('')
      checkboxItemsEmpty && this.$set('checkboxItems', items)

      this.$nextTick(() => {
        this._initCheckboxSlot()
      })

      return this
    },

    /**
     * checkboxItems  slot
     */
    _initCheckboxSlot() {
      if (this.slotItems.length === 0) {
        return false
      }

      if (typeof this.compileVm === 'undefined') {
        this.compileVm = this.$parent
      }

      $(this.$el).find(`.${this.cPrefix}-opt-slot .item`).each((index, el) => {
        if (this.slotItems[index]) {
          let $el = $(el)
          let dom = document.createElement('div')

          dom.innerHTML = this.slotItems[index]
          this.compileVm.$compile(dom)
          el.appendChild(dom.firstChild)
        }
      })
    },

    /**
     *  checkbox  value 
     *
     * @param {String, Number} - checkbox 
     */
    _changeCheckbox(val) {
      let hasDelflag = false

      this.value.every((item, index) => {
        if (val === item) {
          hasDelflag = true
          this.value.splice(index, 1)

          return false
        }

        return true
      })

      if (hasDelflag) {
        return this
      }

      return this.value.push(val)
    },

    /**
     * click 
     */
    _handlerClick(event, index) {
      this.check(event, index)
      // this.motion.splice(index - 1, 1, false)
    },

    /**
     * Mousedown 
     */
    _handlerMousedown(event, index) {
      if (this.inTouch) {
        return false
      }

      this.allowFocus = false
    },

    /**
     * Mouseup 
     */
    _handlerMouseup(event, index) {
      if (this.inTouch) {
        return false
      }

      this.allowFocus = true
    },

    /**
     * Keyup 
     */
    _handlerKeyup(event, index) {
      if (event.keyCode === 13) {
        return this.check(event, index)
      }
    },

    /**
     * focus 
     */
    _handlerFocus(event, index) {
      if (this.allowFocus) {
        this.motion.splice(index - 1, 1, true)
      }
    },

    /**
     * blur 
     */
    _handlerBlur(event, index) {
      this.motion.splice(index - 1, 1, false)
    }
  },

  created() {
    this._initCheckboxItems()

    this._initCheckbox()
  }
}

export default checkCompConfig



