/**
 * btn 
 *
 * @prop ban - 
 * @prop block - 
 * @prop link - 
 * @prop radius - none | S | M | L
 * @prop size - 
 * @prop submit - 
 * @prop type -  (button | flat | float | outline)
 * @prop textDisplay - 
 * @prop value - 
 *
 * @event click - btn
 * @event keyEnter - focus  Enter 
 * @event focus
 * @event blur
 */

import './Btn.scss'
import './Btn.material.scss'
import './Btn.bootstrap.scss'

import {
  offset as propOffset
} from '../../util/dom/prop'

import render from './Btn.render'
import baseMixin from '../../mixin/base'
import formMixin from '../../mixin/form'

import Loading from '../Loading/Loading'
import MotionRip from '../MotionRip/MotionRip'

const BTN_TYPE_LINK = 'link'
const BTN_TYPE_BUTTON = 'button'

const SIZE_S = 'S'
const SIZE_M = 'M'
const SIZE_L = 'L'

export default {
  name: 'Btn',

  mixins: [baseMixin, formMixin],

  render,

  components: {
    loading: Loading,
    'motion-rip': MotionRip
  },

  props: {
    ban: {
      type: Boolean,
      default: false
    },
    block: {
      type: Boolean,
      default: false
    },
    kind: {
      type: String,
      default: 'primary'
    },
    link: String,
    outline: {
      type: Boolean,
      default: false
    },
    radius: {
      type: String,
      default: 's',
      validator(val) {
        const size = val.toLowerCase()

        return ['none', 's', 'm', 'l'].includes(size)
      }
    },
    type: {
      type: String,
      default: BTN_TYPE_BUTTON,
      validator(val) {
        return ['button', 'float', 'flat', 'outline'].includes(val)
      }
    },
    value: {
      type: String,
      require: true
    },
    size: {
      type: String,
      default: SIZE_S,
      validator(val) {
        const size = val.toLowerCase()

        return ['s', 'm', 'l'].includes(size)
      }
    },
    submit: {
      type: Boolean,
      require: false
    },
    textDisplay: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      banState: false, // 
      btnValueDisplay: false, // 
      createdLoading: false, //  loading 
      focusing: false, //  focus 
      motion: false, // 
      allowFocus: true, //  focus 
      mousePoi: { // 
        top: 0,
        left: 0
      },
      inTouch: false // 
    }
  },

  watch: {
    ban(val) {
      this.banState = val
    }
  },

  computed: {
    cPrefix() {
      return `${this.compPrefix}-btn`
    },
    isLink() {
      return !this.btnValueDisplay && this.link
    },
    isFloatBtn() {
      return this.type === 'float'
    },
    btnClass() {
      return this.xclass([
        this.themeClass,
        this.uiClass,
        `size-${this.size.toLowerCase()}`,
        `radius-${this.radius.toLowerCase()}`,
        `type-${this.type}`
      ])
    }
  },

  methods: {
    _setDataOpt() {
      this.banState = this.ban
    },

    mouseup(event) {
      if (this.inTouch) {
        return false
      }

      if (this.banState) {
        return false
      }

      this.allowFocus = true

      if (event.button === 0) {
        return this.click()
      }
    },

    /**
     * TODO:  IE <= 11 html  margin pageX  margin 
     */
    mousedown(event) {
      if (this.inTouch) {
        return false
      }

      if (this.banState) {
        return false
      }

      let el = event.currentTarget

      this.allowFocus = false

      if (this.UIMaterial) {
        const elOffset = propOffset(el)

        this.mousePoi = {
          x: event.pageX - elOffset.left,
          y: event.pageY - elOffset.top
        }

        this.$refs.transition && this.$refs.transition.enter({
          mousePoi: {
            ...this.mousePoi
          }
        })
      }
    },

    focus(event) {
      this.focusing = true

      this.$emit('focus', {
        event,
        emitter: this
      })

      if (this.inTouch) {
        return false
      }

      if (this.allowFocus) {
        this.motion = true
      }
    },

    blur(event) {
      this.focusing = false

      this.$emit('blur', {
        event,
        emitter: this
      })

      this.motion = false
    },

    /**
     * keyup 
     */
    keyup(event) {
      if (event.keyCode === 13) {
        this.click()

        return this.$emit('keyEnter', {
          event,
          emitter: this
        })
      }
    },

    /**
     * click event
     */
    click(event) {
      return this.$emit('click', {
        event,
        emitter: this
      })
    },

    /**
     * 
     */
    banBtn() {
      this.banState = true
    },

    /**
     * 
     */
    allowBtn() {
      this.banState = false
    },

    /**
     * 
     */
    openLoading() {
      if (!this.createdLoading) {
        this.createdLoading = true
        this.banBtn()
      }

      this.$nextTick(() => {
        this.$refs.loading.show()
      })
    },

    /**
     * 
     */
    classLoading(state) {
      this.allowBtn()
      this.$refs.loading.hide()
    }
  }
}



