/**
 * select-option --  select  option 
 *
 * @prop multiple - 
 * @prop option - option
 * @prop optRoot - 
 * @prop valName -  options  value  key name
 * @prop txtName -  options  text  key name
 *
 * @event change - checkboxoption
 * @event changeScroller - /
 */

import './SelectOpt.scss'
import Vue from 'vue'
import render from './SelectOpt.render'
import compEvent from '../../config/event.json'

import iconComp from '../Icon/Icon'
import checkComp from '../Check/Check'
import listComp from '../List/List'
import rowComp from '../Row/Row'
import colComp from '../Col/Col'

import MotionRip from '../MotionRip/MotionRip'
import baseMixin from '../../mixin/base'

export default {
  name: 'SelectOpt',

  render,

  mixins: [baseMixin],

  components: {
    icon: iconComp,
    check: checkComp,
    list: listComp,
    row: rowComp,
    column: colComp,
    'motion-rip': MotionRip
  },

  props: {
    option: {
      type: Array,
      default: () => []
    },

    multiple: {
      type: Boolean,
      default: false
    },

    optRoot: {
      type: Object,
      default: () => {
        return {}
      }
    },

    valName: {
      type: String,
      default: 'value'
    },

    txtName: {
      type: String,
      default: 'text'
    }
  },

  data() {
    return {
      selectedAllCheckOpt: [{ //  check  option
        value: -1,
        text: ''
      }],
      focusIndex: 0, //  option 
      pressing: false,
      optionEleH: 0 // 
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-select-opt`
    },
    optionLength() {
      return this.option.length
    }
  },

  methods: {
    _binder() {
      this.$refs.list.$on('change', () => {
        this.$emit('changeScroller', {
          emitter: this
        })
      })
    },

    /**
     *  keydown 
     */
    keydown(direction) {
      this.optionEleH = this.$refs.option1.offsetHeight

      switch (direction) {
        case 'up':
          this.focusIndex = this.focusIndex === 0 ? 0 : this.focusIndex - 1
          this.$refs.list.scrollTop(this.optionEleH * (this.focusIndex))
          this.selectOption(this.focusIndex + 1, false)

          break
        case 'down':
          this.focusIndex = this.focusIndex === this.optionLength - 1 ? this.optionLength - 1 : this.focusIndex + 1
          this.$refs.list.scrollTop(this.optionEleH * (this.focusIndex))
          this.selectOption(this.focusIndex + 1, false)

          break
        case 'left':
          break
        case 'right':
          break
        default:
          break
      }
    },

    //  li  class 
    liClass(classify, value) {
      return [
        `${this.cPrefix}-li`,
        this.optRoot.defaultValClassName(value),
        {
          [`${this.cPrefix}-classify-title`]: classify
        }
      ]
    },

    /**
     * @param {Object} 
     * @return {Boolean}
     */
    hasSubOption(item) {
      return Array.isArray(item.sub) && item.sub.length > 0
    },

    /**
     * @param {Object} index -  1 
     * @param {Boolean} hideMenu - 
     *
     * @return {Function}
     */
    selectOption(index, hideMenu = true) {
      const option = this.option[parseInt(index - 1, 10)]

      if (option.classify) {
        return false
      }

      this.$emit('change', {
        emitter: this,
        value: option[this.valName],
        text: option[this.txtName],
        index: index,
        hideMenu
      })
    },

    /**
     * 
     */
    initPagePosition() {
      return this.$refs.list.initPagePosition()
    },

    /**
     * 
     */
    initPageDisplay() {
      return this.$refs.list.initPageDisplay()
    },

    _handlerMouseenter(event, index) {
      this.focusIndex = index - 1
    },

    _handlerClidk(event, index) {
      event && event.stopPropagation()

      const option = this.option[parseInt(index - 1, 10)]

      if (option.classify) {
        return false
      }

      this.$refs[`rip${index}`].enter()

      this.$emit('change', {
        emitter: this,
        value: option[this.valName],
        text: option[this.txtName],
        index: index,
        hideMenu: true
      })
    }
  },

  mounted() {
    this.$nextTick(() => {
      this.optionEleH = this.$refs.option1.offsetHeight
    })
  }
}



