/**
 * table 
 *
 * @prop auto - 
 * @prop border - 
 *   noneallrowcol
 * @prop page - item
 * @prop pager - 
 * @prop pageSize - item
 * @prop list - 
 * @prop thead - 
 * @prop tbody - 
 * @prop scrollerAutoHide - 
 *
 * @event switchPage - 
 */

import './Table.scss'
import render from './Table.render'

import loadingComp from '../Loading/Loading'

import baseMixin from '../../mixin/base'
import listMixin from '../../mixin/list'
import tip from '../Message/tip'
import { findGrandpa } from '../../util/util'

const COL_PADDING_BORDER_LENGTH = 22

const Table = {
  name: 'Table',

  render,

  mixins: [baseMixin, listMixin],

  components: {
    loading: loadingComp
  },

  props: {
    auto: {
      type: Boolean,
      default: false
    },
    scrollerAutoHide: {
      type: Boolean,
      default: false
    },
    border: {
      type: String,
      default: 'none'
    },
    list: {
      type: Boolean,
      default: false
    },
    thead: {
      type: Array,
      default: () => []
    },
    tbody: {
      type: Array,
      default: () => []
    },
    page: {
      type: Object,
      default: () => {
        return {}
      }
    },
    pager: {
      type: Boolean,
      default: false
    },
    pageSize: {
      type: Number,
      default: 5
    }
  },

  data() {
    this.compName = 'table' // 

    return {
      emptyDataText: this.$t('table.emptyData'),
      pageData: {},
      tbodyItem: this.tbody.slice(),
      theadItem: this.thead.slice(),
      tableWidth: 0 // 
    }
  },

  computed: {
    cPrefix() {
      return `${this.compPrefix}-table`
    },
    pagerDisplay() {
      return this.list && this.pager &&
        this.tbody.length > 0 &&
        this.tbodyItem.length > 0
    }
  },

  watch: {
    tbody(val) {
      if (this.auto) {
        this.initPage({
          tableData: val.slice()
        })
      }

      this.initTable({
        pageNum: this.pageData.current,
        tableData: val.slice()
      })
    },
    thead(val) {
      this.theadItem = val.slice()
    },
    deviceSize() {
      this.tableWidth = this.$el.offsetWidth
    }
  },

  methods: {
    _initComp() {
      this.tableWidth = this.$el.offsetWidth
    },

    /**
     * 
     */
    initPage({ tableData = {}, pageData = {} }) {
      if (!this.auto) {
        this.pageData = Object.assign({}, pageData)

        return this
      }

      this.pageData = Object.assign(pageData, {
        length: tableData.length,
        size: this.pageSize,
        current: 1,
        total: Math.ceil(tableData.length / this.pageSize)
      })

      return this
    },

    /**
     * 
     *
     * @param { Object } - 
     *
     * @return { Object }
     */
    initTable({ pageNum = 1, tableData }) {
      this.tbodyItem = this.getListItemByPage({
        listItem: tableData,
        pageNum,
        pageSize: this.auto ? this.pageSize : false
      })

      return this
    },

    /**
     * loading 
     *
     */
    hideLoading() {
      this.$refs.loading.hide()

      return this
    },

    /**
     * loading 
     *
     */
    showLoading() {
      this.$refs.loading.show()

      return this
    },

    switchPage(currentPage) {
      this.showLoading()

      this.initTable({
        pageNum: currentPage,
        tableData: this.tbody.slice()
      })

      this.hideLoading()

      return this.$emit('switchPage', {
        currentPage,
        emitter: this
      })
    },

    scroll() {
      return this.$emit('scroll')
    }
  },

  created() {
    this.initPage({ tableData: this.tbody.slice() }).initTable({
      pageNum: this.pageData.current,
      tableData: this.tbody.slice()
    })
  }
}

export default Table



